@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap');

:root {
  --primary-hue: 204;
  --primary-color: hsl(var(--primary-hue), 70%, 53%);
  --primary-light: hsl(var(--primary-hue), 70%, 65%);
  --primary-dark: hsl(var(--primary-hue), 70%, 40%);
  --secondary-color: hsl(145, 63%, 49%);
  --accent-color: hsl(6, 78%, 57%);
  --background-color: hsl(192, 15%, 94%);
  --text-color: hsl(210, 29%, 29%);
  --shadow-color: hsla(0, 0%, 0%, 0.1);
  
  --spacing-xs: 0.25rem;
  --spacing-sm: 0.5rem;
  --spacing-md: 1rem;
  --spacing-lg: 2rem;
  --spacing-xl: 4rem;

  --border-radius-sm: 5px;
  --border-radius-md: 10px;
  --border-radius-lg: 15px;
  --border-radius-full: 9999px;

  --transition-fast: 150ms ease;
  --transition-normal: 300ms ease;
  --transition-slow: 500ms ease;

  --font-family: 'Poppins', sans-serif;
  --font-size-base: 16px;
  --font-size-sm: 0.875rem;
  --font-size-lg: 1.25rem;
  --font-size-xl: 1.5rem;
  --font-size-2xl: 2rem;
  --font-size-3xl: 3rem;

  --font-weight-normal: 400;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
}

*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: var(--font-size-base);
  scroll-behavior: smooth;
}

body {
  background-color: var(--background-color);
  font-family: var(--font-family);
  color: var(--text-color);
  line-height: 1.6;
  overflow-x: hidden;
}

.app {
  max-width: min(90%, 800px);
  margin: var(--spacing-xl) auto;
  text-align: center;
  background: #fff;
  border-radius: var(--border-radius-lg);
  padding: var(--spacing-lg);
  box-shadow: 0 10px 30px var(--shadow-color);
}

h1, h2 {
  margin-bottom: var(--spacing-md);
  line-height: 1.2;
}

h1 {
  color: var(--primary-color);
  font-size: var(--font-size-3xl);
  font-weight: var(--font-weight-bold);
  text-transform: uppercase;
  letter-spacing: 2px;
}

h2 {
  color: var(--secondary-color);
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-semibold);
}

button {
  background-color: var(--primary-color);
  color: white;
  padding: var(--spacing-sm) var(--spacing-lg);
  border: none;
  border-radius: var(--border-radius-full);
  cursor: pointer;
  transition: all var(--transition-normal);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-semibold);
  text-transform: uppercase;
  letter-spacing: 1px;
  margin: var(--spacing-xs);
  outline: none;
  position: relative;
  overflow: hidden;
}

button::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: width 0.6s ease-out, height 0.6s ease-out;
}

button:hover::before {
  width: 300%;
  height: 300%;
}

button:hover {
  background-color: var(--primary-light);
  transform: translateY(-3px);
  box-shadow: 0 5px 15px var(--shadow-color);
}

button:active {
  transform: translateY(-1px);
  box-shadow: 0 2px 5px var(--shadow-color);
}

.clicks {
  font-size: var(--font-size-lg);
  margin: var(--spacing-lg) 0;
  padding: var(--spacing-md);
  background-color: hsla(var(--primary-hue), 70%, 53%, 0.1);
  border-radius: var(--border-radius-md);
}

.click-count {
  font-weight: var(--font-weight-bold);
  color: var(--accent-color);
  font-size: var(--font-size-xl);
}

.achievement-list {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: var(--spacing-md);
  margin-top: var(--spacing-lg);
}

.achievement {
  background-color: #fff;
  padding: var(--spacing-md);
  border-radius: var(--border-radius-md);
  transition: all var(--transition-normal);
  box-shadow: 0 5px 15px var(--shadow-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.achievement:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px var(--shadow-color);
}

@media (max-width: 600px) {
  :root {
    --font-size-base: 14px;
  }

  .app {
    margin: var(--spacing-md);
    padding: var(--spacing-md);
  }
  
  h1 {
    font-size: var(--font-size-2xl);
  }
  
  h2 {
    font-size: var(--font-size-lg);
  }
  
  .achievement-list {
    grid-template-columns: 1fr;
  }
}

@keyframes pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

.pulse {
  animation: pulse 1s infinite;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

.float {
  animation: float 3s ease-in-out infinite;
}

@media (prefers-reduced-motion: reduce) {
  *, ::before, ::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}